import React, { useState, useEffect } from "react";
import styled from "styled-components";

import { useNavigate } from "react-router-dom";

import Select from "../list/MainList";
import PostList from "../list/PostList";


import data from "../../data.json";



const Wrapper = styled.div`
min-width:20%;
min-height:20%;
width: 100vw;
height: 100%;

display: flex;
flex-direction: row;
align-items: flex-start;
justify-content: space-between;
background-color:#FDFDFE;

`;

const Container = styled.div`
width: 100%;
max-width:100%;
margin-left:8px;
overflow-x: hidden;
height:100vh;
& > *{
    :not(:last-chilld){
        margin-bottom: 16px;
    }
}
background-color:white;
border-radius: 8px;

`;

const Enter = styled.div`
margin:32px; `

const Line = styled.div`
width:100%;
height:1px;
background:#E6EAF0;
border-radius:3px;
`

const styles = {
    flex: {
        display: "flex",
        justifyContent: "flex-end",
        gap: "16px",
        padding: "12px",
        paddingRight: "36px",
    },
    timecolor: {
        color: "#999999"
    }
}

function Clock() {
    const [time, setTime] = useState(new Date());

    useEffect(() => {
        const interval = setInterval(() => {
            setTime(new Date());
        }, 1000);

        // cleanup function to clear the interval when component unmounts
        return () => clearInterval(interval);
    }, []); // empty dependency array ensures effect runs only once

    return (
        <div style={styles.timecolor}>
            현재 시간:  {time.toLocaleTimeString()}
        </div>
    );
}


function MainPage(props) {

    const navigate = useNavigate();
    // const element = <h2>현재 시간: {new Date().toLocaleTimeString()}</h2>

    return (
        <Wrapper>
            <Select />
            <Container>
                <Enter>
                    <h2>새 글</h2> <Clock />
                </Enter>
                <Line />
                <PostList posts={data} onClickItem={(p) => { navigate('/post/' + p.id + 'database') }}></PostList>

            </Container >
        </Wrapper >
    );
}

// setInterval(MainPage, 1000);


export default MainPage;