import React, { useState } from "react";
import styled from "styled-components";
import { useNavigate, useParams } from 'react-router-dom';


import CommentList from "../list/CommentList";
import Button from "../ui/Button";
import TextInput from "../ui/TextInput";

import data from "../../data.json";


import Select from "../list/MainList";
import Logo from "../ui/Top";

const Wrapper = styled.div`

width: 100vw;
min-height:600px;
height: 100vh;
display: flex;
flex-direction: row;
align-items: flex-start;
justify-content: space-between;
background-color:#242424;
`;

const Container = styled.div`

width: 100%;
max-width:100%;
min-height:600px;
height:100vh;
overflow-x:hidden;
margin-left:32px;
margin-right:32px;
& > *{
    :not(:last-child){
        margin-bottom:16px;
    }
}
`;

const PostContainer = styled.div`
background-color: #ffffff;
overflow-x:hidden;
padding: 16px;
margin:32px;
border: 1px #DEE5EB;
border-radius:8px;
min-height:980px;
height:90vh;
`;

const TitleText = styled.p`
font-size: 28px;
font-weight: 500;
`;

const ContentText = styled.p`
display:flex;
width:100%;
flex-direction:column;
justify-content:center;
height:420px;
overflow:scroll;
font-size: 20px;
line-height:32px;
white-space: pre-wrap;
`;

const CommentLabel = styled.p`
font-size:16px;
font-weight: 500;
padding: 16px;
`;

const Line = styled.div`
width:100%;
height:1px;
background:#242424;
border-radius:3px;
`

const Commentcontainer = styled.div`
background:#F4F4F6;
border-radius:8px;
margin-top:16px;
height:260px;
overflow:scroll;
`

function PostViewPage(props) {
    const navigate = useNavigate();
    const postId = useParams().id

    const post = data.find((item) => {
        return item.id = postId;
    });

    const [comment, setComment] = useState('최소 20자 이상 작성해주세요. 비방과 부적절한 욕설은 삼가해주세요');

    return (
        <Wrapper>
            <Select> </Select>
            <Container>

                <PostContainer>
                    <h4 title="뒤로 가기" onClick={() => { navigate('/') }}>뒤로가기</h4>

                    <TitleText>{post.title}</TitleText>
                    <Logo />
                    <Line />
                    <ContentText> <img src={post.comments[0].i} alt="testimg" /> <div>{post.content}</div>
                    </ContentText>
                    <Line />
                    <Commentcontainer>
                        <CommentLabel>댓글</CommentLabel>
                        <CommentList comments={post.comments}></CommentList>

                        <TextInput height={60} value={comment} onChange={(e) => setComment(e.target.value)}></TextInput>
                        <Button title="댓글 작성하기" onClick={() => { alert('댓글 작성하기!') }}></Button>

                    </Commentcontainer>

                </PostContainer>

            </Container>
        </Wrapper >
    );
}

export default PostViewPage;