import React, { useState } from "react";
import styled from "styled-components";
import { useNavigate } from 'react-router-dom';

import Button from "../ui/Button"
import TextInput from "../ui/TextInput";
import Select from "../list/MainList";


const Wrapper = styled.div`

width: 100vw;
height: 100vh;
display: flex;
flex-direction: row;
align-items: flex-start;
justify-content: space-between;
background-color:#242424;
`;

const Container = styled.div`
padding:32px;
width: 100%;
max-width: 100%;
height:90%;
margin-left:32px;
display: flex;
flex-direction: column;
color:#f5f5f5;
border-radius: 8px;

`;

const styles = {
    p: {
        color: "#999999",
        fontWeight: "200",
        textAlign: "right",
        paddingRight: "32px"
    }
}

function PostWritePage(props) {
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [date] = useState(new Date());

    const navigate = useNavigate();

    return (
        <Wrapper>
            <Select />
            <Container>
                <h4 title="뒤로 가기" onClick={() => { navigate('/') }}>뒤로가기</h4>
                <h1>글 작성하기</h1>
                <h2 style={styles.p}> 업로드 날짜:  {date.toLocaleDateString()}</h2>
                <h4>Title</h4>
                <TextInput height={20} value={title} onChange={(e) => setTitle(e.target.value)}></TextInput>
                <h4>Content</h4>
                <TextInput height={480} value={content} onChange={(e) => setContent(e.target.value)}></TextInput>

                <Button title="글 작성하기" onClick={() => { navigate('/') }}></Button>
            </Container>
        </Wrapper >
    )
}

export default PostWritePage;