import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
width: calc(100% -64px);
padding: 4px 16px;
display: flex;
flex-direction: column;
align-items: flex-start;
border: 1px solid #DEE5EB;
border-radius: 8px;
cursor: pointer;
background: white;
&:hover{
    background: lightgrey;
}
`;

const styles = {
    radius: {
        width: "36px",
        height: "36px",
        objectFit: "cover",
        borderRadius: "48px"
    }
}

const image = [
    {
        id: 0,
        numb: "stefan-stefancik-QXevDflbl8A-unsplash.jpg"
    }

]

const ContentText = styled.p`
min-width:800px;
width:72vw;
font-size: 14px;
white-space: pre-wrap;
display:flex;
align-items:center;
gap:16px;
`;

function CommentItem(props) {

    const { comment } = props;

    return (
        <Wrapper>
            <ContentText><img style={styles.radius} width={40} src={process.env.PUBLIC_URL + "/img/" + image[0].numb} alt="" />{comment.content} </ContentText>
        </Wrapper>
    );
}

export default CommentItem;